    padding: 0.001rem 0.75rem;
}





.btn-light {
    background  : $light;
    border-color: $light;
    color       : $white;
    
    
    &:active,
    &:focus,
    &:hover {
        background  : lighten($light, 25%);
        color       : $dark;
        border-color: lighten($light, 25%);
    }
}

.btn-outline-primary{
	&:hover{
		color:$white;
	}
}
.btn-outline-danger{
	&:hover{
		color: $white;
	}
}
.btn-outline-warning{
	&:hover{
		color:$white;
	}
}
.btn-outline-info{
	&:hover{
		color:$white;
	}
}
.btn-outline-success{
	&:hover{
		color:$white;
	}
}
.btn-outline-secondary{
	color:$secondary;
	@at-root [data-theme-version="dark"] & {
		color:$secondary;
	}
}
.btn-outline-dark{
	@at-root [data-theme-version="dark"] & {
		color:$dark;
	}
}


.btn-dark {
    background  : $dark;
    border-color: $dark;
    color       : $white;
    
    
    &:active,
    &:focus,
    &:hover {
        background  : darken($dark, 10%);
        color       : $white;
        border-color: darken($dark, 10%);
    }
}

.btn-group{

    &.btn-rounded{

        .btn:first-child{
            border-top-left-radius: 30px;
            border-bottom-left-radius: 30px;
        }

        .btn:last-child{
            border-top-right-radius: 30px;
            border-bottom-right-radius: 30px;
        }
    }
}



